import { ShareIcon } from "@rp/react/web/common/base-components"
import { Box, ToggleButton } from "@rp/react/web/mui/core"
import { HowToReg } from "@rp/react/web/mui/icons"
import { MonochromeToggleButtonGroup } from "../MonochromeToggleButtonGroup"

type Props = {
  value: number
  setValue: (value: number) => void
  size?: "small" | "medium" | "large"
}

const HubWatchlistsToggleTabs = ({ value, setValue, size = "medium" }: Props) => {
  return (
    <Box>
      <MonochromeToggleButtonGroup
        sx={{ "& svg": { marginRight: "4px" } }}
        exclusive
        value={value}
        onChange={(_, newValue) => setValue(newValue ?? value)}
        size={size}
      >
        <ToggleButton sx={{ width: 148 }} value={0}>
          <HowToReg />
          My Watchlists
        </ToggleButton>
        <ToggleButton sx={{ width: 170 }} value={1}>
          <ShareIcon shared size={24} />
          Shared Watchlists
        </ToggleButton>
      </MonochromeToggleButtonGroup>
    </Box>
  )
}

export { HubWatchlistsToggleTabs }
